.container-date-type {
	margin: 0 auto;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	line-height: 26px;
}

.element-date-type {
	width: 30%;
	text-align: center;
	font-size: 16px;
	cursor: pointer;
}

@media screen and (min-width: 768px) {
	.element-date-type {
		font-size: 2rem;
	}
}
